import { Component, OnInit } from '@angular/core';
import { ApiSeafoodService } from '../api-seafood.service';

@Component({
  selector: 'app-seafood',
  templateUrl: './seafood.component.html',
  styleUrls: ['./seafood.component.css']
})
export class SeafoodComponent implements OnInit {

 
  seafood: any[] =[];

  constructor(private _api: ApiSeafoodService) { }

  ngOnInit(): void {
    let ob = this._api.getAllSeafood();
    ob.subscribe((res:any)=>{
      this.seafood = res.meals;
    });
  }  

}
