import { Component, OnInit } from '@angular/core';
import { ApiCategoryService } from '../api-category.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  category: any[] =[];

  constructor(private _api: ApiCategoryService, private _route: Router) { }

  ngOnInit(): void {
    let ob = this._api.getAllCategory();
    ob.subscribe((res:any)=>{
      this.category = res.categories;
    });
  }

  detailCategory(idCategory:number){
    this._route.navigate(['category',idCategory]);
  }

}
