import { Component, OnInit } from '@angular/core';
import { ApiFilterByAreaService } from '../api-filter-by-area.service';

@Component({
  selector: 'app-filter-by-area',
  templateUrl: './filter-by-area.component.html',
  styleUrls: ['./filter-by-area.component.css']
})
export class FilterByAreaComponent implements OnInit {

  filterbyarea: any[] =[];

  constructor(private _api: ApiFilterByAreaService) { }

  ngOnInit(): void {
    let ob = this._api.getAllFilterByArea();
    ob.subscribe((res:any)=>{
      this.filterbyarea = res.meals;
    });
  }  

}
