import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiMealService } from '../api-meal.service';

@Component({
  selector: 'app-meal',
  templateUrl: './meal.component.html',
  styleUrls: ['./meal.component.css']
})
export class MealComponent implements OnInit {

  meal: any[] =[];
  constructor(private _api: ApiMealService, private _route: Router) { }

  ngOnInit(): void {
    let ob = this._api.getAllMeal();
    ob.subscribe((res:any)=>{
      this.meal = res.meals;
    });  
  }

  detailMeal(idMeal:number){
    this._route.navigate(['meal',idMeal]);
  }
  

}
