import { Component, OnInit } from '@angular/core';
import { ApiNonvegService } from '../api-nonveg.service';

@Component({
  selector: 'app-non-veg',
  templateUrl: './non-veg.component.html',
  styleUrls: ['./non-veg.component.css']
})
export class NonVegComponent implements OnInit {

  nonveg: any[] =[];

  constructor(private _api: ApiNonvegService) { }

  ngOnInit(): void {
    let ob = this._api.getAllNonveg();
    ob.subscribe((res:any)=>{
      this.nonveg = res.meals;
    });
  }

}
