import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CategoryComponent } from './category/category.component';
import { FilterByAreaComponent } from './filter-by-area/filter-by-area.component';
import { HomeComponent } from './home/home.component';
import { MealComponent } from './meal/meal.component';
import { NonVegComponent } from './non-veg/non-veg.component';
import { SeafoodComponent } from './seafood/seafood.component';


const routes: Routes = [
  {path: "", component: HomeComponent},
  {path: "meal", component: MealComponent},
  {path: "category", component: CategoryComponent},
  {path: "nonveg", component: NonVegComponent},
  {path: "seafood", component: SeafoodComponent},
  {path: "areafilter", component: FilterByAreaComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
